import React, { useContext } from "react";
import { useTranslation } from "react-i18next";
import { Navbar, Nav, NavDropdown, Container } from "react-bootstrap";
import AppContext from "store/app-store";
import styles from "./MenuPages.module.css";

const menuItems = [
  { path: "about-us", textId: "Header.AboutUs" },
  {
    path: "medical-tourism",
    textId: "Header.Medical-Tourism",
    subItems: [
      {
        path: "medical-tourism/why-medical-tourism",
        textId: "Header.Why-Medical-Tourism",
      },
      {
        path: "medical-tourism/is-it-right-for-me",
        textId: "Header.Is-it-right-for-me",
      },
      {
        path: "medical-tourism/essentials-to-pack",
        textId: "Header.Essentials-to-pack",
      },
    ],
  },
  {
    path: "turkey",
    textId: "Header.Turkey",
    subItems: [
      { path: "turkey/about-turkey", textId: "Header.AboutTurkey" },
      { path: "turkey/why-turkey", textId: "Header.WhyTurkey" },
    ],
  },
  {
    path: "cities",
    textId: "Header.Cities",
    subItems: [
      { path: "cities/istanbul", textId: "Header.Cities-Istanbul" },
      { path: "cities/antalya", textId: "Header.Cities-Antalya" },
    ],
  },
  { path: "health-providers", textId: "Header.HealthProviders" },
  { path: "faq", textId: "Header.FAQ" },
  { path: "what-to-expect", textId: "Header.WhatToExpect" },
];

const MenuItem = ({ item, currentLang, translate, linkColorClass }) => {
  return item.subItems ? (
    <NavDropdown
      title={<span className={linkColorClass}>{translate(item.textId)}</span>}
      // title={translate(item.textId)}
      id={`nav-dropdown-${item.textId}`}
      className={linkColorClass}
      // className={`${linkColorClass} nav-dropdown`}
      style={{ color: "white" }}
    >
      {item.subItems.map((subItem, idx) => (
        <NavDropdown.Item
          key={idx}
          href={`/${currentLang}/${subItem.path}`}
          // className={linkColorClass}
        >
          {translate(subItem.textId)}
        </NavDropdown.Item>
      ))}
    </NavDropdown>
  ) : (
    <Nav.Link href={`/${currentLang}/${item.path}`} className={linkColorClass}>
      {translate(item.textId)}
    </Nav.Link>
  );
};

const MenuPages = ({ isScrolled, toggleLanguage }) => {
  const { t } = useTranslation();
  const { getLang } = useContext(AppContext);
  const currentLang = getLang();
  const linkColorClass = isScrolled ? styles.scrolledLink : styles.defaultLink;
  const languageToggleClass = `${styles.languageToggle} ${isScrolled ? styles.scrolled : ''}`;

  return (
    <Navbar expand="lg" className={linkColorClass}>
      <Container>
        <Nav className={`me-auto main-nav ${linkColorClass}`}>
          {menuItems.map((item, idx) => (
            <MenuItem
              key={idx}
              item={item}
              currentLang={currentLang}
              translate={t}
              linkColorClass={linkColorClass}
            />
          ))}

          <Nav.Link onClick={toggleLanguage} className={languageToggleClass}>
            {currentLang === "ar" ? "English" : ""}
          </Nav.Link>          {/* <a
            onClick={toggleLanguage}
            style={{ cursor: "pointer", color: isScrolled ? "#555" : "#fff" }}
          >
            {appContext.getLang() === "ar" ? "English" : ""}
          </a> */}
          {/* Uncomment and add logic if login/logout is needed */}
          {/* {isLoggedIn ? (
            <Nav.Link onClick={onLogout}>{t("Logout")}</Nav.Link>
          ) : (
            <Nav.Link href="/login">{t("Login")} / {t("Signup")}</Nav.Link>
          )} */}
        </Nav>
      </Container>
    </Navbar>
  );
};

export default MenuPages;
